<template>
  <v-form mt-10 ref="form">
    <v-container>
      <h1 class="title"></h1>
      <br />
      <span>ProfileImage</span>
      <v-file-input
        accept="image/png, image/jpeg, image/bmp"
        enctype="multipart/form-data"
        placeholder="Pick an avatar"
        prepend-icon="mdi-camera"
        label="File input"
        @change="selectFile"
      ></v-file-input>
      <template v-slot:selection="{ index, text }">
        <v-chip v-if="index < 2" color="deep-purple accent-4" dark label small>
          {{ text }}
        </v-chip>

        <span
          v-else-if="index === 2"
          class="text-overline grey--text text--darken-3 mx-2"
        >
          +{{ files.length - 2 }} File(s)
        </span>
      </template>

      <span></span>
      <v-text-field
        v-model="name"
        :counter="10"
        :rules="nameRules"
        label="Name"
        required
        outlined
      ></v-text-field>
      
      <v-text-field
        v-model="email"
        :rules="emailRules"
        label="E-mail "
        required
        outlined
      ></v-text-field>
      
      <v-text-field
        type="password"
        v-model="password"
        :rules="passwordRules"
        label="password "
        required
        outlined
      ></v-text-field>

       
      <v-text-field
        type="password"
        v-model="password2"
        label="password "
        required
        outlined
      ></v-text-field>

      <v-btn
        block
        :disabled="!buttonActivation"
        color="success"
        class="mr-4"
        @click="validate"
      >
        go register
      </v-btn>
    </v-container>
  </v-form>
</template>

<script>
import axios from "axios";
// import axios from "axios";

export default {
  data() {
    return {
      valid: false,
      name: "",
      password: "",
      password2: "",
      email: "",
      password_confirmation: true,

      nameRules: [
        (v) => !!v || " .",
        (v) => (v && v.length <= 10) || " 10  .",
      ],
      emailRules: [
        (v) => !!v || " .",
        (v) => /.+@.+\..+/.test(v) || "   .",
      ],
      passwordRules: [
        (v) => !!v || " .",
        (v) => v.length > 7 || "  8  .",
      ],
    };
  },
  computed: {
    buttonActivation: function() {
      if (
        this.name === "" ||
        this.password === "" ||
        this.password2 === "" ||
        this.email === "" ||
        this.password !== this.password2 ||
        /.+@.+\..+/.test(this.email) == false
      ) {
        return false;
      } else return true;
    },
  },
  methods: {
    validate() {
      if (this.password != this.password2) {
        return alert("  .");
      } else {
        this.password_confirmation = true;
      }

      const form = new FormData();
      form.append("image", this.image);
      form.append("email", this.email);
      form.append("name", this.name);
      form.append("password", this.password);
      form.append("password_confirmation", this.password_confirmation);
      axios
        .post("http://35.76.24.183:8000/api/register", form)
        .then(() => {
          // console.log(res.data);
          this.$router.push("/login");
        })
        .catch((err) => {
          console.log(" ");
          if (err.response.data.email.length == 1) {
            alert("   .   .");
          } else
            alert("   .   .");
          this.email = "";
        });
    },
    selectFile(file) {
      this.image = file;
    },
  },
};
</script>

<style scoped>
.title {
  text-align: center;
}
.container {
  max-width: 500px;
}
</style>
